.Input {
    background-color: rgb(202, 126, 54);
    color: rgb(70, 44, 15);
    /* border-radius: 5%; */
    height: 40px;
    width: 70vw;
    border:0.2em solid rgb(202, 126, 54);
    margin: 2px;
    font-size:1em;
}

::placeholder {
    color: rgb(112, 68, 27);
    font-size:1em;
}
.Button {
    background-color: rgb(177, 108, 44);
    color: rgb(70, 44, 15);
    /* border-radius: 5%; */
    height: 45px;
    width: 10vw;
    border:0.2em solid rgb(177, 108, 44);
    margin: 2px;
    font-size:1em;
}

.Button:hover {
    background-color: rgb(136, 83, 34);
}
