.Nav {
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
}
.Nav :hover {
    background-color: rgb(155, 96, 41);
    border: none;
}
.Button {
    width: 50px;
    height: 50px;
    background-color: rgb(202, 126, 54);
    border: none;
    margin: 1px;
    color: rgb(70, 44, 15);
    font-size:1em;
}
.Button :hover {
    background-color: rgb(155, 96, 41);
}
.CurrentButton {
    width: 50px;
    height: 50px;
    background-color: rgb(155, 96, 41);
    border: none;
    margin: 1px;
    color: rgb(70, 44, 15);
    font-size:1em;
}


ul {
    padding: 0;
}

ul :hover {
    background-color: rgb(155, 96, 41);
}