body {
    background-color: rgb(175, 79, 0);
}

.Container {
    background-image: url(../images/homeImage.jpg);
    height: 60vh;
    width: 98,5vw;
    margin: 0;
    text-align: center;
    position: relative;
    background-position: center;
    background-repeat:no-repeat;
    background-size: cover;
}

.Link {
    text-decoration: none;
}

.Filter {
    background-color: rgb(141, 88, 38);
    color: rgb(54, 35, 13);
    /* border-radius: 10%; */
    height: 30px;
    width: 15vw;
    text-align: center;
    border:0.2em solid rgb(202, 126, 54);
    margin: 2px;
    margin-bottom: 10px;
}

.Cards {
    width: 60vw;
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 20vw;
}

.Paginado {
    width: 50vw;
}

.ContainerFilters {
    height:50vh;
    padding-top: 5vh;
}

.Button {
    width: 300px;
    height: 30px;
    background-color: rgb(77, 48, 24);
    border: 0.2em solid rgb(202, 126, 54);
    margin: 1px;
    color: rgb(228, 147, 56);
    font-size:1em;
    margin-bottom: 10px;
}

.Button:hover {
    background-color: rgb(146, 91, 46);
}

