.Container {
    background-image: url(../images/homeImage.jpg);
    height: 100vh;
    width: 98,5vw;
    margin: 0;
    position: relative;
    background-position: center;
    background-repeat:no-repeat;
    background-size: cover;
    background-color: black;
    
}

.ContainerForm {
    background-color: rgb(0, 0, 0);
    width: 50%;
    height: 80%;
    margin:0 25% 0 25%;
    border-radius: 5%;
    
}


.ContainerForm h2 {
    color: rgb(202, 126, 54);
    padding-top:30px;
}

.Input {
    background-color: black;
    color: white;
    font:1em;
    padding:0.4em;
    border:0.2em solid rgb(31, 31, 31);
    align-items:flex-end;
    text-align: right;
    
}

.Input input{
    background-color: rgb(29, 29, 29);
    padding: 0.5em;
    border:0.2em solid rgb(2, 2, 2);
    width: 70%;
    margin: 0 5px 0 5px;
    right: 0;
    color: white;
    

}

.Parrafo{
    margin:0;
    color:red;
}

.Select {
    background-color: black;
    color: white;
    font:1em;
    padding:0.4em;
    border:0.2em solid rgb(31, 31, 31);
}

.Button {
    font: 1em;
    padding:0.4em;
    background-color: rgb(71, 71, 71);
    color: white;
    border:0.2em solid rgb(34, 34, 34);
}

.Button:hover {
    color:rgb(2, 2, 2);
}

.DeleteContainer {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-around;
    padding: 0 50px 0 50px;
}

.LittleBox {
    display: flex;
    width: 150px;
    justify-content: flex-end;
    height: 25px;
    margin: 0 5px 0 5px;
}

.LittleBox p{
    margin: 0 5px 0 0;
    color: grey;
}

.LittleBox button{
    background-color: black;
    color:white;
}