.Container {
    background-color: rgb(202, 126, 54);
    height: 270px;
    width: 160px;
    border-radius: 10%;
    margin: 1em;
    padding: 1.5em;
    display: flexbox;
    
}
.Container :hover {
    background-color: rgb(228, 143, 64);
}

.Img {
    height: 100px;
    width: 150px;
    border-radius: 10%;
    z-index: auto;

}

h4{
    margin-top: 0;
    
}

.Detail {
    color: rgb(46, 24, 6);
    margin: 10px 0 10px 0;
    padding: 2px;
    
}

a :hover {
    background-color: rgb(228, 143, 64);
}

